import logo from './logo.svg';
import './App.css';
import Person from './components/Person';
import React from 'react';

function App() {
  let[number,setNumber] = React.useState("");

  return (
    <div>
      <Person id={number} />
      <input
        type="number"
        name="number"
        onChange={(e) => {
          setNumber(e.target.value);
        }}
      />
    </div>
  );
}

export default App;

